<template>
  <div class="cropper-wrapper">
    <!-- 头部标题栏 -->
    <div class="cropper-header">
      <button class="header-btn" @click="cancel">取消</button>
      <h3 class="header-title">图片裁剪</h3>
      <button class="header-btn confirm" @click="confirm">完成</button>
    </div>

    <!-- 裁剪区域 -->
    <div class="cropper-container">
      <vue-cropper ref="cropper" :img="imageSrc" :auto-crop="true" :fixed="fixedRatio" :fixed-number="ratio"
        :center-box="true" :high="true" @real-time="realTime" mode="none" :original="true"></vue-cropper>
    </div>

    <!-- 比例选择 -->
    <div class="ratio-selector" v-if="showRatioSelector">
      <button v-for="(item, index) in ratioOptions" :key="index"
        :class="['ratio-btn', { active: currentRatio === item.value }]" @click="setRatio(item.value)">
        {{ item.label }}
      </button>
    </div>

    <!-- 缩放控制 -->
    <div class="zoom-controls">
      <button class="zoom-btn" @click="zoomOut">
        <span class="zoom-icon">-</span>
      </button>
      <input type="range" disabled v-model="zoomValue" min="0" max="50" step="0" class="zoom-slider">
      <button class="zoom-btn" @click="zoomIn">
        <span class="zoom-icon">+</span>
      </button>
    </div>
  </div>
</template>

<script>
  import {
    VueCropper
  } from 'vue-cropper'

  export default {
    components: {
      VueCropper
    },
    props: {
      imageSrc: {
        type: String,
        required: true
      },
      showRatioSelector: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        fixedRatio: false,
        currentRatio: null,
        ratio: [1, 1],
        zoomValue: 0, // 初始缩放值为0%
        cropSize: {},
        ratioOptions: [{
            label: '自由',
            value: null
          },
          {
            label: '1:1',
            value: 1
          },
          {
            label: '4:3',
            value: 4 / 3
          },
          {
            label: '16:9',
            value: 16 / 9
          }
        ]
      }
    },
    mounted() {
      this.setRatio(null)
    },
    methods: {
      setRatio(ratio) {
        this.currentRatio = ratio
        this.fixedRatio = ratio !== null
        this.ratio = ratio ? [ratio, 1] : [1, 1]
      },
      realTime(data) {
        let width = this.getNumber(data.div.width),
          height = this.getNumber(data.div.height)
        this.cropSize = {
          width,
          height
        }
      },
      getNumber(str) {
        const newStr = str.replace('px', '');
        return Number(newStr)
      },
      confirm() {
        this.$refs.cropper.getCropBlob(blob => {
          // 转为缩放前尺寸
          this.resizeBlobImage(blob, this.cropSize.width / (this.zoomValue == 0 ? 1 : this.zoomValue*0.02+1), this.cropSize.height / (this.zoomValue == 0 ? 1 : this.zoomValue*0.02+1)).then((resizedBlob) => {
            this.blobToBase64(resizedBlob).then(base64 => {
              this.$emit('confirm', base64);
            });
          }).catch((error) => {
            console.error('Error:', error);
          });
        })
      },
      resizeBlobImage(blob, newWidth, newHeight) {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.onload = function(e) {
            const img = new Image();
            img.src = e.target.result;
            img.onload = function() {
              const canvas = document.createElement('canvas');
              const ctx = canvas.getContext('2d');
              canvas.width = newWidth;
              canvas.height = newHeight;
              ctx.drawImage(img, 0, 0, newWidth, newHeight);
              canvas.toBlob((resizedBlob) => {
                resolve(resizedBlob);
              }, blob.type);
            };
            img.onerror = function() {
              reject(new Error('Failed to load image'));
            };
          };
          reader.onerror = function() {
            reject(new Error('Failed to read blob'));
          };
        });
      },
      blobToBase64(blob) {
        return new Promise((resolve) => {
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.onloadend = () => {
            resolve(reader.result);
          };
        });
      },
      cancel() {
        this.$emit('cancel')
      },
      zoomIn() {
        if (this.zoomValue != 50) {
          this.zoomValue += 1
          this.$refs.cropper.changeScale(1)
        }
      },
      zoomOut() {
        if (this.zoomValue != 0) {
          this.zoomValue -= 1
          this.$refs.cropper.changeScale(-1)
        }
      }
    }
  }
</script>

<style scoped>
  /* 样式保持不变 */
  .cropper-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f8f8f8;
    z-index: 1000;
    display: flex;
    flex-direction: column;
  }

  .cropper-header {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    background: white;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  }

  .header-title {
    font-size: 17px;
    font-weight: 500;
    color: #333;
    margin: 0;
  }

  .header-btn {
    border: none;
    background: transparent;
    font-size: 15px;
    color: #666;
    padding: 8px 12px;
  }

  .header-btn.confirm {
    color: #07C160;
  }

  .cropper-container {
    flex: 1;
    position: relative;
    background: #333;
  }

  .ratio-selector {
    padding: 15px;
    background: white;
    display: flex;
    justify-content: space-around;
    border-top: 1px solid #eee;
  }

  .ratio-btn {
    padding: 8px 15px;
    border-radius: 15px;
    border: 1px solid #ddd;
    background: white;
    color: #666;
    font-size: 14px;
  }

  .ratio-btn.active {
    background: #07C160;
    color: white;
    border-color: #07C160;
  }

  .zoom-controls {
    padding: 15px;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #eee;
  }

  .zoom-slider {
    flex: 1;
    margin: 0 15px;
    height: 30px;
  }

  .zoom-btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #f0f0f0;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: #333;
  }

  .zoom-icon {
    line-height: 1;
  }
</style>