<template>
  <div class="image-marker-app">
    <!-- 应用头部 - 带返回按钮的样式 -->
    <header class="header-bar">
      <button class="back-btn" @click="goBack">
        <svg viewBox="0 0 24 24">
          <path d="M15.41,16.58L10.83,12L15.41,7.41L14,6L8,12L14,18L15.41,16.58Z"/>
        </svg>
      </button>
      <h1 class="header-title">图片裁剪工具</h1>
      <div class="header-placeholder"></div>
    </header>

    <!-- 主内容区 -->
    <main class="app-main">
      <!-- 上传区域 -->
      <div v-if="!selectedImage" class="upload-area" @dragoover.prevent="dragOver" @dragleave="dragLeave" @drop.prevent="handleDrop">
        <label class="upload-label" :class="{ 'is-dragging': isDragging }" @click="v2==true?'':getIntent()">
          <input 
            v-if="v2==true"
            type="file" 
            accept="image/jpeg,image/png,image/webp" 
            @change="handleFileSelect"
            class="upload-input"
          >
          <div class="upload-content">
            <svg class="upload-icon" viewBox="0 0 24 24">
              <path d="M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2M18,20H6V4H13V9H18V20Z M8,15V13H16V15H8M8,11V9H16V11H8M8,19V17H13V19H8Z"/>
            </svg>
            <h3 class="upload-title">选择或拖拽图片到此</h3>
            <p class="upload-hint">支持 JPG、PNG 或 WEBP 格式</p>
          </div>
        </label>
      </div>

      <!-- 裁剪结果区域 -->
      <div v-else class="result-area">
        <div class="result-header">
          <h3>裁剪结果</h3>
          <button @click="clearAll" class="clear-btn">
            <svg class="clear-icon" viewBox="0 0 24 24">
              <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z"/>
            </svg>
            重新选择
          </button>
        </div>
        
        <div class="result-image-container" @click="openPreview">
          <img :src="croppedImage || selectedImage" class="result-image" alt="裁剪后的图片">
        </div>
      </div>

      <!-- 裁剪弹框 -->
      <transition name="modal">
        <div v-if="showCropper" class="modal-mask">
          <div class="modal-wrapper">
            <div class="modal-container">
              <div class="modal-header">
                <h3>图片裁剪</h3>
                <button class="modal-close" @click="closeCropper">
                  <svg viewBox="0 0 24 24">
                    <path d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z"/>
                  </svg>
                </button>
              </div>
              <div class="modal-body">
                <MobileCropper
                  v-if="showCropper"
                  :image-src="selectedImage"
                  @confirm="handleCropComplete"
                  @cancel="closeCropper"
                />
              </div>
            </div>
          </div>
        </div>
      </transition>

      <!-- 图片预览弹窗 -->
      <transition name="fade">
        <div v-if="showPreview" class="preview-modal" @click.self="closePreview">
          <div class="preview-content">
            <img :src="previewImage" class="preview-image" alt="预览图片">
            <button class="preview-close" @click="closePreview">
              <svg viewBox="0 0 24 24">
                <path d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z"/>
              </svg>
            </button>
          </div>
        </div>
      </transition>
    </main>

    <!-- 应用页脚 -->
    <footer class="app-footer">
