</template>

<script>
import MobileCropper from '@/components/MobileCropper.vue';

export default {
  name: 'ImageCropperTool',
  components: {
    MobileCropper
  },
  data() {
    return {
      v2: this.$globalConfig.v2,
      selectedImage: '',
      croppedImage: '',
      showCropper: false,
      isDragging: false,
      showToast: false,
      toastMessage: '',
      toastType: 'success',
      showPreview: false,
      previewImage: ''
    };
  },
  mounted() {
    if(this.v2!=true) {
      window.handleImageResult = this.handleAutoJsCall;
    }
  },
  methods: {
    goBack() {
      this.$goBackOrHome()
    },
    
    // 图片预览功能
    openPreview() {
      this.previewImage = this.croppedImage || this.selectedImage;
      this.showPreview = true;
      document.body.style.overflow = 'hidden';
    },
    
    closePreview() {
      this.showPreview = false;
      document.body.style.overflow = '';
    },

    // 文件处理
    handleFileSelect(event) {
      const file = event.target.files[0];
      if (file) this.processImageFile(file);
    },
    
    handleDrop(event) {
      this.isDragging = false;
      const file = event.dataTransfer.files[0];
      if (file) this.processImageFile(file);
    },
    
    dragOver() {
      this.isDragging = true;
    },
    
    dragLeave() {
      this.isDragging = false;
    },
    
    processImageFile(file) {
      if (!file.type.match(/image\/(jpeg|png|webp)/)) {
        this.showNotification('请选择JPG、PNG或WEBP格式的图片', 'error');
        return;
      }
      
      if (file.size > 5 * 1024 * 1024) {
        this.showNotification('图片大小不能超过5MB', 'error');
        return;
      }
      
      const reader = new FileReader();
      reader.onload = (e) => {
        this.selectedImage = e.target.result;
        this.showCropper = true;
      };
      reader.onerror = () => {
        this.showNotification('图片加载失败', 'error');
      };
      reader.readAsDataURL(file);
    },
    
    // 调用 Autox
    getIntent() {
      window.Android.invoke('bitmapIntent', { msg: '打开文件选择器' }, (data) => {})
    },
    // Autox回调
    handleAutoJsCall(data) {
      if(data.code == 200) {
        this.selectedImage = data.base64;
        this.showCropper = true;
      }
    },
    
    // 裁剪完成处理
    handleCropComplete(base64Image) {
      this.croppedImage = base64Image;
      this.showCropper = false;
      this.showNotification('裁剪完成', 'success');
    },
    
    // 关闭裁剪器
    closeCropper() {
      this.showCropper = false;
    },
    
    // 清除所有
    clearAll() {
      this.selectedImage = '';
      this.croppedImage = '';
      this.previewImage = '';
    },
    
    // 通知提示
    showNotification(message, type = 'success') {
      this.toastMessage = message;
      this.toastType = type;
      this.showToast = true;
      
      setTimeout(() => {
        this.showToast = false;
      }, 3000);
    }
  }
};
</script>

